import Vue from'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter);

const router = new VueRouter({
	routes:[{
		path:'/home',component:require('../components/content/main')
	},{
		path:'/share',component:require('../components/content/share')
	},{
		path:'/search',component:require('../components/content/search')
	},{
		path:'/alarm',component:require('../components/content/alarm')
	},{
    path:'*',redirect:'/home'
  }]
})

export default router


