<template>
	<div class="mainnews">
		<div class="nav">
			<ul>
				<li class="hot" v-on:click="chot"><span>热门</span></li>
				<li class="new"v-on:click="cnew"><span>最新</span></li>
				<li class="comment"v-on:click="ccomment"><span>评论</span></li>
			</ul>
		</div>
		<div>
		<div v-for="onenews in news.hot.data"v-show="hotnews">
			<vnews  v-bind:data="onenews"></vnews>
		</div>
		<div v-for="onenews in news.new.data"v-show="newnews">
			<vnews  v-bind:data="onenews"></vnews>
		</div>
		<div v-for="onenews in news.comment.data"v-show="comment">
			<vnews  v-bind:data="onenews"></vnews>
		</div>
		</div>
			
	</div>
</template>
<script type="text/javascript">
	import vnews from "./news"
	const ERR_OK = 0
	export default{
		data:function(){
			return{
				news:{},
				hotnews:true,
				newnews:false,
				comment:false
			}
		},
		/*props:{
			news:{
				type:Object
			}
		},*/
		components:{
			vnews
		},
		methods:{
			chot:function(){
				this.hotnews=true
				this.comment=false
				this.newnews=false
			},
			cnew:function(){
				this.newnews=true
				this.comment=false
				this.hotnews=false
			},
			ccomment:function(){
				this.comment=true
				this.newnews=false
				this.hotnews=false
			}
		},
		beforeCreate(){
			/*let hot = this.news.hot
			console.log(this.news.hot);*/
		},
		created(){
			this.$http.get('/api/news').then(function(res){
	  		res = res.body
	  		if (res.errno===ERR_OK) {
	  			this.news = res.data
	  		}
	  	})
		}

		
	}
</script>
<style lang="stylus" scoped>
	.mainnews
		width 100%
		margin-top 20px
		border-top 1px solid rgb(216, 216, 216)
		.nav
			padding-left 25px
			height 40px
			margin-top 5px 
			border-bottom 1px solid rgb(216, 216, 216)
			ul 
				margin-top 20px
				li 
					float left	
					margin-right 20px
					font-size 20px
					border-right 1px solid rgb(200, 200, 200)
					padding-right 20px	
					color rgb(171, 181, 191)
					&:last-child
						border-right 0
</style>


