<template>
	<div class="share">
		<div class="ashare">
			<i><a href="">分享指南</a></i>
		</div>
		<div class="content">
			<div class="title">
				<h2>推荐文章到掘金</h2>
				<span>感谢分享，文章的审核时间约为1-2个工作日</span>
			</div>
			<div class="toshare">
				<div class="shareinput">
					<div><input id="shareurl" type="text" placeholder="分享网址"/></div>
					<div><input id="sharetitle" type="text" placeholder="标题"/></div>
					<div><textarea id="sharedescribe" placeholder="描述"></textarea></div>
				</div>
				<div class="upload">
					<i><span>拖动一张照片进来</span></i><br>
					<span>或</span>
					<a href="">点击上传</a>
				</div>	
			</div>
			<div class="column">
				<div class="classification">
				分类
				<ul v-for="(label,index) in sharelabel.classlabel.data">
					<li v-bind:class="{'active':active===label.name}" v-on:click="clicklabel(label.name)"><a href="javascript:;">{{label.name}}</a></li>
				</ul>
				</div>
				<div class="other">
				其他
				<ul v-for="(label,index) in sharelabel.otherlabel.data">
					<li v-bind:class="{'active':active===label.name}" v-on:click="clicklabel(label.name)"><a href="javascript:;">{{label.name}}</a></li>
				</ul>
				</div>
			</div>
			
		</div>
	</div>
	
</template>
<script>
	const ERR_OK = 0
	export default{
		data:function(){
			return {
				active :'',
				sharelabel:{}
			}
		},
		methods:{
			clicklabel:function(name){
				if (this.active==name) {
					this.active=''
				}else{
					this.active=name
				}
				
			}
		},
		created(){
			this.$http.get('/api/sharelabel').then(function(res){
				res = res.body
				if (res.errno === ERR_OK) {
					this.sharelabel = res.data
					let data = this.sharelabel.classlabel.data					
					for (var i = 0; i < data.length; i++) {
						data[i].isActive=false
					}
					this.sharelabel.classlabel.data=data
				}
			})
		}
	}
</script>
<style lang="stylus" scoped>
	.share 
		height 100%
		.ashare
			height 25px
			background rgb(216, 216, 216)
			i 
				font-size 12px
				margin-left 30px
				line-height 25px
		.content 
			display flex
			height 100%
			flex-direction column
			.title 
				width 100%
				flex 1
				h2
					font-size 20px
					font-weight  bold
			.toshare
				display flex
				width 100%
				flex 2
				.shareinput
					flex 2
					display flex
					flex-direction column
					div
						flex 1
						height 100%
					:last-child
						flex 2
					#shareurl
						border 1px solid #452635
						height 30px
					#sharetitle
						border 1px solid #452635
						height 30px
					#sharedescribe
						border 1px solid #452635
						height 100px
				.upload
					flex 1
					border 1px solid #452635
			.column
				display flex
				width 100%
				flex 2
				margin-top 10px
				.classification
					flex 2
				.other
					flex 1
				ul
					margin-top 20px
					.active
						border 1px solid rgb(216, 236, 255)
						background rgb(243, 249, 255)
						a
							color rgb(29, 142, 255)
					li 
						float left
						width 20%
						margin-right 10px
						margin-bottom 10px
						padding 5px
						border 1px solid rgba(216, 216, 216,0.5)
						border-radius 2px
						font-size 10px
						text-align center
</style>


